<template>
  <div class="category" ref="category">
    <loading v-show="isLoading" />
    <van-nav-bar left-text="返回" left-arrow :title="major" @click-left="goBack" />
    <van-tabs
      v-model="index"
      title-inactive-color="#999"
      animated
      color="steelblue"
      title-active-color="steelblue"
      sticky
      line-width="50px"
      @click="toggleTab"
    >
      <van-tab :title="val.name" v-for="(val,i) in bookData" :key="i">
        <div class="content-list">
          <van-list
            class="popup-list"
            v-model="load.loading"
            :finished="load.finished"
            :finished-text="load.loadingMsg"
            @load="onLoad"
            :offset="150"
          >
            <div
              class="content clearfix"
              v-for="(item , index) in category"
              :key="index"
              @click="goBook(item._id)"
            >
              <div class="img-box fl">
                <img class="auto-img" v-lazy="getImg(item.cover)" alt="图片未加载..." />
              </div>
              <div class="text-box fl">
                <div class="text-title">{{item.title}}</div>
                <div class="label-box clearfix">
                  <span class="name fl">{{item.author}}</span>
                  <span class="label fl">{{item.majorCate}}</span>
                </div>
                <div class="text-content">{{item.shortIntro}}</div>
                <div class="text-data clearfix">
                  <span class="fl text">
                    <span class="num">{{item.latelyFollower | num}}</span> 万人气
                  </span>
                  <span class="fl text">
                    <span class="num">{{item.retentionRatio}}%</span> 留存
                  </span>
                </div>
              </div>
            </div>
          </van-list>
        </div>
      </van-tab>
    </van-tabs>
    <span class="top-icon" @click.stop="backTop">
      <img class="auto-img" :src="require('../assets/images/top-icon.png')" alt />
    </span>
  </div>
</template>

<script>
import loading from "../components/loading";
import "../assets/css/category.less";
export default {
  components: { loading },
  data() {
    return {
      isLoading: false,
      index: 0,
      bookData: [
        { name: "最热", type: "hot" },
        { name: "最新", type: "new" }
      ],
      // 全部数据
      categoryData: [],
      // 默认加载数据
      category: [],
      gender: "", // 主分类
      type: "hot", // 最热/最新
      major: "", // 类型

      // 默认加载目录数量
      loadCount: 10,
      load: {
        // 是否加载 false表示加载
        // true为停止加载
        loading: false,
        finished: false,
        loadingMsg: "已经到底啦"
      }
    };
  },
  created() {
    // 获取分类传过来的数据
    this.gender = this.$route.query.gender;
    this.major = this.$route.query.major;
    this.toggleTab();
  },
  methods: {
    // 标签切换 获取分类书籍详情
    toggleTab(index) {
      this.isLoading = true;
      this.index == 0 ? (this.type = "hot") : (this.type = "new");
      this.index = index;
      this.axios({
        method: "get",
        url: "http://novel.kele8.cn/category-info",
        params: {
          gender: this.gender,
          type: this.type,
          major: this.major,
          minor: "",
          start: 0,
          limit: 100
        }
      })
        .then(result => {
          if (result.status == 200) {
            this.categoryData = result.data.books;
            this.category = this.categoryData.splice(0, this.loadCount);
            if (this.categoryData.length > 0) {
              this.load.loading = false;
            } else {
              this.load.loading = true;
              this.load.finished = true;
            }
            if (this.category.length == 0) {
              this.load.loadingMsg = "";
            }
            this.isLoading = false;
          }
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    // 图片解码
    getImg(val) {
      return unescape(val).slice(7);
    },
    // 返回
    goBack() {
      this.$router.go(-1);
    },
    // 带id跳转到书籍详情页
    goBook(id) {
      this.$router.push({ name: "Book", query: { id: id } });
    },
    // 懒加载书籍数据
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        // 添加章节
        this.category.push(...this.categoryData.splice(0, this.loadCount));
        // 默认加载数据
        this.load.loading = false;
        // 如果没有数据则停止加载
        if (this.categoryData.length == 0) {
          this.load.loading = true;
          this.load.finished = true;
        }
      }, 500);
    },
    // 动态返回页面顶部
    backTop() {
      if (this.$refs.category.scrollTop != 0) {
        let timer = setInterval(() => {
          let speed = (0 - this.$refs.category.scrollTop) / 20;
          this.$refs.category.scrollTop += speed;
          if (this.$refs.category.scrollTop <= 0) {
            clearInterval(timer);
            return;
          }
        }, 18);
      }
    }
  }
};
</script>
